import React, { useContext, useEffect, useRef, useState } from "react"
import "../../styles/bitmoji.scss"
import _ from "lodash"
import $ from 'jquery';
import BitmojiTooltip from './BitmojiTooltip';

import bitmoji from "../../images/bitmoji.png"
import bitmojiHover from "../../images/bitmojiClosed.png"
import bitmojiScroll from "../../images/bitmojiScroll.png"

const Bitmoji = props => {
  const bitmojiRef = useRef(null);
  const [showTooltip, setShowTooltip] = useState(false);
  const { toggleDarkMode } = useContext(props.context);

  useEffect(() => {
    const handleScroll = (e) => {
      bitmojiRef.current.src = bitmojiScroll;
    }
  
    const handleScrollStop = _.debounce(() => {
      bitmojiRef.current.src = bitmoji;
    }, 200);

    window.addEventListener('scroll', handleScroll, { passive: true });
    window.addEventListener('scroll', handleScrollStop);

    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('scroll', handleScrollStop);
    }
  }, []);
  
  const handleClick = (e) => {
    $('html,body').animate({ scrollTop: 0 }, 2000);
    toggleDarkMode();
  }

  const handleMouseOver = (e) => {
    e.currentTarget.src = bitmojiHover;
    setShowTooltip(true);
  }

  const handleMouseOut = (e) => {
    e.currentTarget.src = bitmoji;
    setShowTooltip(false);
  }

  return (
    <>
      <div className="bitmoji-container">
        <div className="container center-align">
          <div className="bitmoji-wrapper">
            <img
              className="responsive-img stick-to-footer"
              ref={bitmojiRef}
              id="bitmoji-welcome"
              src={bitmoji}
              alt="bitmoji"
              role="button"
              onClick={handleClick}
              onMouseOver={handleMouseOver}
              onFocus={handleMouseOver}
              onMouseOut={handleMouseOut}
              onBlur={handleMouseOut}
            />
            {/* {showTooltip && (
              <BitmojiTooltip page={props.page} context={props.context} content="Click me!" />
            )} */}
          </div>
        </div>
      </div>
    </>
  );
}

export default Bitmoji;
